import { HttpClient } from '@angular/common/http';
import { TranslateLoader } from '@ngx-translate/core';
import { Observable, of } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import { Optional, Inject, Injectable } from '@angular/core';
import { LOCALE_PATH } from './locale.path';
import { LANGUAGES } from './languages';

@Injectable()
export class TranslateCustomLoader implements TranslateLoader {
  constructor(
    private http: HttpClient,
    @Inject(LOCALE_PATH) private path: string = null,
    @Inject(LANGUAGES) private languages: any = null
  ) {}

  getTranslation(lang: string): Observable<any> {
    const path = this.path || '';
    let url = `${path}${lang}.json`;
    if (this.languages && this.languages[lang] && this.languages[lang].url) {
      url = this.languages[lang].url;
    }
    return this.http.get(url).pipe(
      map((d) => d[lang]),
      catchError(() => of({}))
    );
  }
}
