import { Component, ContentChild, HostBinding, Input } from '@angular/core';
import { NgModel } from '@angular/forms';
import { Subscription, timer } from 'rxjs';
import { debounce, filter } from 'rxjs/operators';
import { MessagesComponent } from './messages.component';
/**
 * A form group helps to validate an input of a form element.
 *
 * ## Example:
 *
 * ```html
 *  <c8y-form-group [hasWarning]="user.email.length === 0">
 *   <label translate for="userEmail">Email</label>
 *   <input
 *     id="userEmail"
 *     class="form-control"
 *     type="email"
 *     name="email"
 *     [maxlength]="254"
 *     autocomplete="off"
 *     placeholder="{{'e.g. joe.doe@example.com' | translate}}"
 *     [(ngModel)]="user.email"
 *     email
 *     required
 *   >
 *   <c8y-messages>
 *     <c8y-message *ngIf="user.email.length === 0" translate></c8y-message>
 *     <c8y-message name="required" text="The E-Mail is SUPER required"></c8y-message>
 *   </c8y-messages>
 *  </c8y-form-group>
 * ```
 *
 * @param status The current status could be error, warning or success.
 * @param hasError Set this to true to display a error.
 * @param hasWarning Set this to true to display a warning.
 * @param hasSuccess Set this to true to display a success.
 * @param novalidation Set this to true to disable automatic validation by this component.
 */
@Component({
  selector: 'c8y-form-group',
  templateUrl: './form-group.component.html',
  host: {
    class: 'form-group'
  }
})
export class FormGroupComponent {
  @Input() hasError = false;
  @Input() hasWarning = false;
  @Input() hasSuccess = false;
  @Input() novalidation = false;
  @Input() status: string;
  @HostBinding('class.has-error') get error() { return this.status === 'error' || this.hasError; }
  @HostBinding('class.has-warning') get warning() { return this.status === 'warning'  || this.hasWarning; }
  @HostBinding('class.has-success') get success() { return this.status === 'success' || this.hasSuccess; }
  @ContentChild(MessagesComponent, {static: false}) customErrorMessage: MessagesComponent;
  @ContentChild(NgModel, {static: true}) model: NgModel;

  errors: {};
  private subscription: Subscription;
  private readonly VALIDATION_DEBOUNCE_MS = 1000;

  ngAfterContentInit(): void {
    this.initNgModel();
  }

  initNgModel() {
    if (this.model) {
      this.subscription = this.model.valueChanges.pipe(
        filter(() => this.model.dirty && !this.novalidation),
        debounce(() => this.hasError ? timer(10) : timer(this.VALIDATION_DEBOUNCE_MS)),
      ).subscribe(() => this.update());
    }
  }

  update() {
    this.hasError = !this.model.valid;
    if (this.customErrorMessage) {
      this.customErrorMessage.changVisibility(this.model.errors);
    } else {
      this.errors = this.model.errors;
    }
  }

  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }
}
