import { Component, Injector } from '@angular/core';
import { ActivatedRoute, NavigationEnd, PRIMARY_OUTLET, Router, UrlSegmentGroup, UrlTree } from '@angular/router';
import { ApiService, ApiCall } from '@c8y/ngx-components/api';
import { NEVER, Observable, Subject, Subscription } from 'rxjs';
import { filter, map, merge, switchMap } from 'rxjs/operators';
import { Tab } from '../tabs/tab.model';
import { TabsService } from '../tabs/tabs.service';
import { RouterTabsResolver } from './router-tabs.resolver';
import { ViewContextServices } from './view-context.service';

@Component({
  selector: 'c8y-context-route',
  templateUrl: './context-route.component.html'
})
export class ContextRouteComponent {
  private dataSubscription: Subscription;
  private routerSubscription: Subscription;
  private lastAddedTabs = [];
  private refreshTrigger = new Subject();

  constructor(
    private tabsResolver: RouterTabsResolver,
    private tabsService: TabsService,
    private route: ActivatedRoute,
    private router: Router,
    private apiService: ApiService,
    private injector: Injector
  ) {}

  ngOnInit(): void {
    this.routerSubscription = this.router.events
      .pipe(filter(e => e instanceof NavigationEnd))
      .subscribe(() => this.redirectToFirstTab());
    const refreshObservable = this.refreshTrigger.pipe(
      merge(this.updatedContext()),
      switchMap(() => this.tabsResolver.resolve(this.route.snapshot))
    );
    this.dataSubscription = this.route.data.pipe(
      map(({ tabs }) => tabs),
      merge(refreshObservable)
    ).subscribe((tabs) => this.updateTabs(tabs));
  }

  ngOnDestroy(): void {
    this.dataSubscription.unsubscribe();
    this.routerSubscription.unsubscribe();
    this.lastAddedTabs.forEach(t => this.tabsService.remove(t));
  }

  refreshTabs() {
    this.refreshTrigger.next();
  }

  updatedContext(): Observable<ApiCall> {
    const { data } = this.route.snapshot;
    const serviceInstance = ViewContextServices.contextToService(data.context);
    if (serviceInstance) {
      const service = this.injector.get(serviceInstance);
      const contextRegex = new RegExp(service.getDetailUrl(data.contextData), 'i');
      const childrenRegex = new RegExp(`${service.getDetailUrl(data.contextData)}/child`, 'i');
      const filterResponse = ({ url, method }) => {
        const contextChanged = contextRegex.test(url) && method === 'PUT';
        const childrenAffected = childrenRegex.test(url) && ['POST', 'DELETE'].includes(method);
        return contextChanged || childrenAffected;
      };
      return this.apiService.hookResponse(filterResponse);
    }
    return NEVER;
  }

  private updateTabs(tabs = []) {
    this.lastAddedTabs.forEach(t => this.tabsService.remove(t));
    this.lastAddedTabs = tabs;
    tabs.forEach(t => this.tabsService.add(t));
    this.redirectToFirstTab();
  }

  private redirectToFirstTab() {
    if (this.needsRedirect()) {
      this.tabsService.firstTab$.subscribe((tab: Tab) => {
        if (tab) {
          this.router.navigateByUrl(tab.path, { replaceUrl: true });
        }
      });
    }
  }

  private needsRedirect() {
    const tree: UrlTree = this.router.parseUrl(this.router.url);
    const groups: UrlSegmentGroup = tree.root.children[PRIMARY_OUTLET];
    const isContextRoute = groups.segments.length === 2;
    return isContextRoute;
  }
}
