import {
  Component,
  ViewChild,
  ElementRef,
  Input,
  OnInit,
  Output,
  EventEmitter
} from '@angular/core';
import { some } from 'lodash-es';

@Component({
  selector: 'c8y-drop-area',
  templateUrl: './drop-area.component.html'
})
export class DropAreaComponent implements OnInit {
  @Input() title = 'Upload file';
  @Input() message = 'Drop file here';
  @Input() icon = 'plus-square';
  @Input() loadingMessage = 'Uploading ...';
  @Input() alwaysShow = false;
  @Input() clickToOpen = true;
  @Input() loading = false;
  @Input() progress = -1; // -1 = spinner
  @Output() dropped: EventEmitter<DroppedFile[]> = new EventEmitter();
  isOver = false;

  fileIsEmpty = false;
  @ViewChild('area', { static: true }) area: ElementRef;
  @ViewChild('zone', { static: false }) zone: ElementRef;
  @ViewChild('picker', { static: false }) picker: ElementRef;

  ngOnInit() {
    this.alwaysShow = this.alwaysShow || this.area.nativeElement.children.length === 0;
  }

  toggle($event?) {
    this.zone.nativeElement.style.height = this.area.nativeElement.offsetHeight + 'px';
    this.onOver();
  }

  showPicker($event?) {
    this.preventDefault($event);
    this.picker.nativeElement.value = '';
    this.picker.nativeElement.click();
  }

  onOver() {
    if (!this.isOver) {
      this.isOver = true;
      document.addEventListener('dragover', this.preventDefault);
      document.addEventListener('drop', this.preventDefault);
    }
  }

  onPick($event) {
    this.fileIsEmpty = false;
    this.preventDefault($event);
    this.onFilesSelected($event.target.files);
  }

  onDrop($event) {
    this.preventDefault($event);
    this.onFilesSelected($event.dataTransfer.files);
    this.stopDragging();
  }

  onFilesSelected(files) {
    this.fileIsEmpty = false;
    if (files && files.length > 0) {
      if (this.isAnyFileEmpty(files)) {
        this.fileIsEmpty = true;
        this.dropped.emit([]);
      } else {
        this.dropped.emit(this.compose(files));
      }
    }
  }

  isAnyFileEmpty(files) {
    return some(Array.from(files), ['size', 0]);
  }

  stopDragging() {
    document.removeEventListener('dragover', this.preventDefault);
    document.removeEventListener('drop', this.preventDefault);
    this.isOver = false;
  }

  private preventDefault($event?) {
    if ($event) {
      $event.preventDefault();
    }
  }

  private compose(files: FileList) {
    return Array.from(files).map(file => ({
      file,
      readAsJson: async () => JSON.parse(await this.read(file, ReadAsType.TEXT)),
      readAsText: async () => this.read(file, ReadAsType.TEXT),
      readAsArrayBuffer: async () => this.read(file, ReadAsType.ARRAY_BUFFER),
      readAsBinaryString: async () => this.read(file, ReadAsType.BINARY_STRING),
      readAsDataURL: async () => this.read(file, ReadAsType.DATA_URL)
    }));
  }

  private async read(file, type: ReadAsType): Promise<string> {
    return new Promise<string>((resolve, reject) => {
      const reader = new FileReader();
      switch (type) {
        case ReadAsType.TEXT: {
          reader.readAsText(file);
          break;
        }
        case ReadAsType.ARRAY_BUFFER: {
          reader.readAsArrayBuffer(file);
          break;
        }
        case ReadAsType.BINARY_STRING: {
          reader.readAsBinaryString(file);
          break;
        }
        case ReadAsType.DATA_URL: {
          reader.readAsDataURL(file);
          break;
        }
      }
      reader.onload = () => this.onLoad(reader, resolve, reject);
    });
  }

  private onLoad(reader, resolve, reject) {
    if (reader.readyState !== 2) {
      return;
    }
    if (reader.error) {
      reject(reader.error);
    }
    resolve(reader.result);
  }
}

export interface DroppedFile {
  file: File;
  readAsText();
  readAsArrayBuffer();
  readAsBinaryString();
  readAsDataURL();
  readAsJson();
}

enum ReadAsType {
  TEXT,
  DATA_URL,
  ARRAY_BUFFER,
  BINARY_STRING
}
