import { Component, forwardRef, Inject } from '@angular/core';
import { CellRendererContext } from '../../data-grid/column/cell-renderer';
import { DeviceGridService } from '../device-grid.service';

@Component({
  template: `
    {{ deviceGridService.getModel(context.item) }}
  `
})
export class ModelCellRendererComponent {
  constructor(
    public context: CellRendererContext,
    @Inject(forwardRef(() => DeviceGridService)) public deviceGridService: DeviceGridService
  ) {}
}
