import { Directive, ElementRef, Injector, Input } from '@angular/core';
import { UpgradeComponent } from '@angular/upgrade/static';

// TODO: find replacement for schema-form ng1 library
@Directive({
  selector: 'c8y-schema-form'
})
export class SchemaFormComponent extends UpgradeComponent {
  @Input() schema: any;
  @Input() model: any;
  @Input('form') inputForm?: any;
  @Input('options') inputOptions?: any;

  constructor(elementRef: ElementRef, injector: Injector) {
    super('c8ySchemaForm', elementRef, injector);
  }
}
