import { Component, forwardRef, Input, OnInit, ViewChild } from '@angular/core';
import { get, cloneDeep } from 'lodash-es';
import { SchemaFormComponent } from '../forms/schema-form.component';
import { FilteringFormRendererContext } from '../data-grid/column/filtering-form-renderer';
import { DeviceGridColumn } from './device-grid.models';

@Component({
  selector: 'c8y-filtering-form',
  templateUrl: './filtering-form.component.html'
})
export class FilteringFormComponent implements OnInit {
  @Input() context: FilteringFormRendererContext;
  @ViewChild(forwardRef(() => SchemaFormComponent), { static: false }) schemaFormComponent: any;

  schema;
  form;
  model;
  options;

  ngOnInit() {
    const column: DeviceGridColumn = this.context.property;
    this.schema = cloneDeep(column.filteringConfig.schema);
    this.form = cloneDeep(column.filteringConfig.form);
    this.model = cloneDeep(column.externalFilterQuery || {});
    this.options = { formDefaults: { ngModelOptions: { debounce: 100 } } };
  }

  canApply() {
    return get(this.schemaFormComponent, '$componentScope.vm.ngForm.$invalid');
  }

  applyFilter() {
    this.context.applyFilter({
      externalFilterQuery: this.model
    });
  }

  resetFilter() {
    this.context.resetFilter();
  }
}
