import { BaseDeviceGridColumn } from './base.device-grid-column';
import { gettext } from '../../i18n/gettext';

export class ImeiDeviceGridColumn extends BaseDeviceGridColumn {
  constructor() {
    super();

    const self = this;

    this.path = 'c8y_Mobile.imei';
    this.name = 'imei';
    this.header = gettext('IMEI');

    this.filterable = true;
    this.filteringConfig = {
      schema: {
        type: 'object',
        properties: {
          imeis: {
            type: 'array',
            items: {
              type: 'string'
            }
          }
        }
      },
      form: [
        {
          key: 'imeis',
          title: gettext('Show devices with IMEI'),
          add: gettext('Or'),
          items: [
            {
              key: 'imeis[]',
              notitle: true,
              placeholder: gettext(
                'e.g. 46543432321, you can use an asterisk (*) as wildcard character'
              )
            }
          ]
        }
      ],
      getFilter(model: any): any {
        const filter: any = {};
        if (model.imeis.length) {
          filter[self.path] = { __in: model.imeis };
        }
        return filter;
      }
    };

    this.sortable = true;
    this.sortingConfig = {
      pathSortingConfigs: [{ path: this.path }]
    };
  }
}
