import { BaseDeviceGridColumn } from './base.device-grid-column';
import { NameCellRendererComponent } from './name.cell-renderer.component';
import { ColumnDataRecordClassName } from '../../data-grid/data-grid.model';
import { gettext } from '../../i18n/gettext';

export class NameDeviceGridColumn extends BaseDeviceGridColumn {
  constructor() {
    super();

    this.name = 'name';
    this.path = 'name';
    this.header = gettext('Name');
    this.cellCSSClassName = ColumnDataRecordClassName.Header;
    this.cellRendererComponent = NameCellRendererComponent;

    this.filterable = true;
    this.filteringConfig = {
      schema: {
        type: 'object',
        properties: {
          names: {
            type: 'array',
            items: {
              type: 'string'
            }
          }
        }
      },
      form: [
        {
          key: 'names',
          title: gettext('Show devices with name'),
          add: gettext('Or'),
          items: [
            {
              key: 'names[]',
              notitle: true,
              placeholder: gettext(
                'e.g. My Device, you can use an asterisk (*) as wildcard character'
              )
            }
          ]
        }
      ],
      getFilter(model: any): any {
        const filter: any = {};
        if (model.names.length) {
          filter.name = { __in: model.names };
        }
        return filter;
      }
    };

    this.sortable = true;
    this.sortingConfig = {
      pathSortingConfigs: [{ path: this.path }]
    };
  }
}
