import {
  ContentChildren,
  Input,
  Output,
  EventEmitter,
  Component,
  ViewChild,
  ElementRef,
  QueryList
} from '@angular/core';
import { ControlContainer, NgForm } from '@angular/forms';
import { fromEvent, Subscription } from 'rxjs';
import { debounceTime, map, distinctUntilChanged, filter } from 'rxjs/operators';
import { IIdentified } from '@c8y/client';
import { BsDropdownDirective } from 'ngx-bootstrap/dropdown';
import { ListItemComponent } from '../list-group/list-item.component';
import { findIndex } from 'lodash-es';

@Component({
  selector: 'c8y-typeahead',
  templateUrl: './typeahead.component.html',
  viewProviders: [{ provide: ControlContainer, useExisting: NgForm }]
})
export class TypeaheadComponent {
  @ViewChild('searchControl', { static: false }) searchControl: ElementRef;
  @ViewChild('dropdown', { static: false }) dropdown: BsDropdownDirective;
  @ContentChildren(ListItemComponent) list: QueryList<ListItemComponent>;

  @Input()
  required: boolean = false;

  @Input()
  placeholder: string;

  @Input()
  displayProperty: string = 'name';

  @Input()
  name: string = this.displayProperty;

  @Input()
  selected: IIdentified = {
    id: null,
    [this.displayProperty]: ''
  };

  @Output()
  selectedChange = new EventEmitter<IIdentified>();

  @Output()
  onSearch = new EventEmitter<string>();

  private subscription: Subscription;
  private readonly KEYCODE_UP = 38;
  private readonly KEYCODE_DOWN = 40;
  private readonly KEYCODE_TAB = 9;
  private readonly KEYCODE_ENTER = 13;
  private readonly KEYCODE_ESC = 27;

  onShown() {
    this.searchControl.nativeElement.focus();
  }

  ngOnDestroy(): void {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }

  ngAfterViewInit(): void {
    this.subscription = fromEvent(this.searchControl.nativeElement, 'keydown')
      .pipe(
        map((e: any) => this.handleKeyboard(e)),
        filter((e: any) => e),
        debounceTime(200),
        map((e: any) => e.target.value),
        distinctUntilChanged()
      )
      .subscribe(value => {
        this.selectedChange.emit({
          id: null,
          [this.displayProperty]: value
        });
        this.onSearch.emit(value);
      });
  }

  handleKeyboard(event): void {
    const keyCode = event.keyCode;
    if (
      [this.KEYCODE_ENTER, this.KEYCODE_DOWN, this.KEYCODE_TAB, this.KEYCODE_UP].includes(keyCode)
    ) {
      const items = this.list.toArray();
      const index = findIndex(items, item => item.active);
      if (keyCode === this.KEYCODE_ENTER || keyCode === this.KEYCODE_TAB) {
        if (index > -1) {
          event.preventDefault();
          items[index].element.nativeElement.click();
        }
        this.dropdown.hide();
        this.searchControl.nativeElement.blur();
      } else {
        this.dropdown.show();
        const upOrDown = keyCode === this.KEYCODE_DOWN ? 1 : -1;
        if (index > -1) {
          items[index].active = false;
        }
        if (items[index + upOrDown]) {
          items[index + upOrDown].active = true;
        }
      }
      return;
    } else if (keyCode === this.KEYCODE_ESC) {
      event.stopPropagation();
      this.dropdown.hide();
      this.searchControl.nativeElement.blur();
      return;
    } else {
      this.dropdown.show();
    }
    return event;
  }
}
